import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {ErrorService} from "../../../globals/services/error.service";
import {AdminEredmenyekHeaderComponent} from "./header.component";
import {APIService} from "../../../globals/services/API.service";
import {AuthService} from "../../../globals/services/auth.service";
import {EredmenyekDeffault} from "./eredmenyekDeffault";
import {globals} from "../../../utils/global.constats";
import {hunkDate} from "../../../globals/pipes/hunkDate.pipe";
import {DateTimePickerComponent} from "../../../components/date-time-picker/DateTimePicker.component";
import {CkeditorComponent} from "../../../components/ckeditor/ckeditor.component";


const URL = globals.backendIp + 'api/eredmenyek_kepek';
@Component({
    moduleId:module.id,
    selector: 'eredmenyek-modify',
    templateUrl: 'eredmenyekModify.html',
    directives: [AdminEredmenyekHeaderComponent,DateTimePickerComponent,CkeditorComponent],
    styleUrls: ['eredmenyek-modify.css'],
    providers: [APIService],
    pipes:[hunkDate]
})
export class EredmenyekModifyComponent implements OnInit {
    eredmenyek:EredmenyekDeffault;
    eredmenyekId:number;
    positions:Array<number>;
    modifying:any;
    file:File;
    htmlValue:string='empty';
    ckeditorAvaible:boolean=false;
    constructor(private _ErrorService:ErrorService,
                private _ApiSrvice:APIService,
                private _AuthService:AuthService,
                private _RouteParams:ActivatedRoute) {
        this.eredmenyek= new EredmenyekDeffault;




    }

;


    onUpdate() {
        if (this.eredmenyek.id) {
            this._ApiSrvice.getResponsePUT('api/eredmenyek/modify/' + this.eredmenyek.id, this.eredmenyek)
                .subscribe(
                    data=> {
                    if (data.errors)
                    {
                        alert(data.errors);
                    }
                    else
                    {
                        window.location.replace("#/admin/eredmenyek");

                    }


                },
                    error=> {
                }
            )

        }
        else {
            this._ApiSrvice.getResponse('api/eredmenyek/', this.eredmenyek)
                .subscribe(
                    data=> {

                        if (data.errors)
                        {

                            alert(data.errors);
                        }
                        else{
                            console.log(data);
                           // this.eredmenyek = data.Eredmenyek;
                            //this.refreshKepek();
                            window.location.replace("#/admin/eredmenyek");

                        }

                },
                    error=> {
                }
            )
        }
    }
    onDelete() {
        var r = confirm("Biztosan törlöd az eredmenyekt?!");
        if (r == true) {
            this._ApiSrvice.getResponsePUT('api/eredmenyek/archive/' + this.eredmenyek.id, '')
                .subscribe(
                    data=> {
                    window.location.replace("#/admin/eredmenyek");


                },
                    error=> {
                })
        }
    }

    onValueChanged(event) {
        this.eredmenyek.content=event;

    }
    ngOnInit() {

      this.eredmenyekId=this._RouteParams.snapshot.params['id'];
        if (this.eredmenyekId) {
            this._ApiSrvice.getResponseGET('api/eredmenyek/'+this.eredmenyekId,'')
                .subscribe(
                    data=> {
                        console.log(data);

                    this.eredmenyek=data;
                        this.ckeditorAvaible=true;

                    //console.log(this.kepek);


                },
                    error=> {
                        console.log(error);
                })
        }
        else {
            this.ckeditorAvaible=true;
        }

    }
    onDateChanged(event){

        const old =this.eredmenyek.datum.split(" ", 2);
        let eredmenydate=event.date.year+'-'+event.date.month+'-'+event.date.day+' '+old[1];
        //console.log(eredmenydate);
        this.eredmenyek.datum=eredmenydate;
      //  console.log(event+'---> '+old);
    }
    change(e){
        this.htmlValue=e;
    }

}
